import ValidaCPF from './validaCPF';


export default class GeraCPF{
    rand(min = 100000000, max = 999999999){
        return String(Math.floor(Math.random() * ( max-min ) + min ));
    }

    geraNovoCPF(){
        const cpfSemDigito = this.rand()
        const digito1 = ValidaCPF.criaDigito(cpfSemDigito);
        const digito2 = ValidaCPF.criaDigito(cpfSemDigito+digito1)
        const cpfComDigitos = cpfSemDigito+digito1+digito2;
        console.log('CPF-CRIADO: ',cpfSemDigito,' digito1 ', digito1,' digito2 ' ,digito2)
        return this.formatado(cpfComDigitos);
    }

    formatado(cpf){
        return(
            cpf.slice(0,3) + '.' +
            cpf.slice(3,6) + '.' +
            cpf.slice(6,9) + '-' +
            cpf.slice(9,11)
        )
    }
}
















